@font-face {
  font-family: Poopis;
  src: url(/public/assets/fonts/Poppins-Regular.ttf);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poopis, Arial, Helvetica, sans-serif
}
body{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.my-node-enter {
  opacity: 0;
}
.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.my-node-exit {
  opacity: 1;
}
.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}
.table{
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 colunas */
  grid-template-rows: repeat(4, 1fr); /* 5 linhas */
  grid-gap: 10px; /* espa√ßamento entre os cards */
}
  .card{
      position: relative;
      width: 100px;
      height: 100px;

      transform-style: preserve-3d;

      transition: .5s;
  }
  .card-back, .card-front{
      overflow: hidden;
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .5rem;
      box-shadow: 1px 1px 20px #0000001f;
      backface-visibility: hidden;
      pointer-events: none;
  }
  .card-front{
      font-size: 2.5rem;
      background-color: rgb(59, 165, 252);
      transform: rotateY(180deg);
  }
  .noflip{
      transform: rotateY(180deg);
  }
      .card-back img{
          width: 100%;
          height: 100%;
      }

@media (max-width: 490px) {
  .card{
    width: 80px;
    height: 80px;
  }
}
.popUp_home, .popUp{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0c4f7c51;
  z-index: 10;
  backdrop-filter: blur(3px);
  padding: 10%;
  color: #fff;


}
  .popUp_home-content, .popUp-content{
    background-color: #181818;
    border-radius: 1rem;
    padding: 2rem;
    box-shadow: 1px 1px 20px #00000067;
    position: relative;
  }
    .popUp_home-content h1, .popUp-content h1{
      text-transform: uppercase;
      line-height: 2rem;
      margin-bottom: 2rem;
    }
    .popUp_home-content #button-rank{
      background-color: #F2F2F2;
      border: none;
      padding: .3rem 1.2rem;
      border-radius: .7rem;
      font-weight: bold;
      cursor: pointer;
      margin-bottom: 2rem;
      transition: .2s;
      box-shadow: 2px 2px rgb(178, 174, 174);
    }
    .popUp_home-content #button-rank:hover{
      box-shadow: none;
      transform: translate(2px, 2px);
    }
    .popUp_home .input_time-container p, .popUp-content p{
      font-size: 1.1rem;
      font-weight: bold;
  }
  .popUp_home .input_time-container input{
      width: 200px;
      margin-top: 1rem;
      font-size: 1.1rem;
      padding: .3rem 1rem;
      border: none;
      border-radius: 1rem;
  }
  .popUp_home .input_time-container input:focus{
      outline: none;
  }
  .popUp_home #button-init{
    background-color: #E8AA42;
    border: 1.5px solid #E8AA42;
    cursor: pointer;
    font-size: 1.2rem;
    text-transform: uppercase;
    padding: .2rem 1.7rem;
    border-radius: 1.2rem;
    margin-top: 3rem;
    transition: .2s;
    font-weight: bold;
  }
  .popUp_home #button-init:hover{
    background-color: transparent;
    color: #E8AA42;
  }


  @media (max-width: 690px) {
    .popUp_home, .popUp{
      padding: 4rem 1rem;
    }
  }

  .popUp-content p{
    margin-top: 1rem;
  }
  .popUp-content article{
    margin-top: 3rem;
  }
  .popUp-content article button{
    margin: .4rem;
    padding: .2rem 1.5rem;
    border: none;
    font-size: 1.2rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: .2s;
    font-weight: bold;
    background-color: #E8AA42;
    border: 1.5px solid #E8AA42;
  }.popUp-content article button:nth-child(2){
    background-color: #47B5FF;
    border: 1.5px solid #47B5FF;
  }
  .popUp-content article button:hover{
    background-color: transparent;
    color: #E8AA42;
  }.popUp-content article button:nth-child(2):hover{
    color: #47B5FF;
  }


  .popUp_rank-options{
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 11;
    background-color: #181818;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-radius: 1rem;

  }
  .popUp_rank-options .edit-user-rank, .popUp_rank-options .list-rank{
    height: 100%;
    padding: 2rem;
    display: flex;
    width: 50%;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    text-align: start;


  }
  .popUp_rank-options h1{
    margin-bottom: 1rem;
  }
  .popUp_rank-options .display-name{
    text-transform: capitalize;
  }
  .popUp_rank-options p{
    font-size: 1.2rem;
    text-transform: uppercase;
    line-height: 1.3rem;
    margin-bottom: 1rem;
    color: #E8AA42;
  }
  .popUp_rank-options .img-coroa{
    margin-top: 2rem;
    width: 100%;
    text-align: start;
  }
  .popUp_rank-options .img-coroa img{
    width: 200px;
  }
  .popUp_rank-options .input-name-user{
    display: flex;
    width: 80%;
  }
  .popUp_rank-options .input-name-user input{
    width: 100%;
    padding: .2rem 1rem;
    transition: .3s;
  }
  .popUp_rank-options .input-name-user input:focus{
    outline: 1.5px solid #E8AA42;
  }
  .popUp_rank-options .input-name-user button{
    background-color: #e8ab42c7;
    border: none;
    cursor: pointer;
    padding: 0 1rem;
    transition: .2s;
  }
  .popUp_rank-options .input-name-user button:hover{
    background-color: #E8AA42;
  }
  .popUp_rank-options .list-rank ul{
    width: 100%;
    min-height: 100%;
    max-height: 600px;
    overflow-y: auto;
    padding: .5rem;
    list-style: none;
    transition: .3s;
  }
  .popUp_rank-options .list-rank li{
    position: relative;
    padding: .4rem .4rem .4rem 2rem;
    margin-bottom: 1rem;
    border: 1.5px solid #fff;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .popUp_rank-options .list-rank li:nth-child(1){
    border: 1.5px solid #E8AA42;
    background-color: #E8AA42;
    color: #000000;
    font-weight: bold;
  }
  .popUp_rank-options .list-rank li:nth-child(2){
    border: 1.5px solid #3ef402;
    background-color: #3ef402;
    color: #000000;
    font-weight: bold;
  }
  .popUp_rank-options .list-rank li:nth-child(3){
    border: 1.5px solid #47B5FF;
    background-color: #47B5FF;
    color: #000000;
    font-weight: bold;
  }
  .popUp_rank-options .list-rank li .position-rank{
    position: absolute;
    left: .7rem;
  }
  #close-popup-rank{
    all: unset;
    position: absolute;
    bottom: 0rem;
    left: 1rem;
    font-size: 1.8rem;
    color: #E8AA42;
    cursor: pointer;
  }

  @media (max-width: 660px) {
    .popUp_rank-options{
      flex-direction: column;
    }
    .popUp_rank-options .edit-user-rank, .popUp_rank-options .list-rank{
      width: 100%;
    }
  }

  .fadeTranslate-enter{
    transform: translateX(-100%);
  }
  
  .fadeTranslate-enter-active {
    transform: translateX(0);
    transition: transform 300ms;
  }

  .fadeTranslate-exit {
    transform: translateX(0)
  }

  .fadeTranslate-exit-active {
    transform: translateX(0);
    transition: transform 300ms;
  }

  .fadeOpacity-enter{
    opacity: 0;
  }
  .fadeOpacity-enter-active{
    opacity: 1;
    transition: opacity 300ms;
  }
  .fadeOpacity-exit{
    opacity: 0;
  }
  .fadeOpacity-exit-active{
    opacity: 1;
    transition: opacity 300ms;
  }